import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation, Pagination, Scrollbar, A11y } from "swiper";
import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";
import "swiper/css/scrollbar";
import { YMaps, Map, Placemark } from "@pbe/react-yandex-maps";
import { Icon } from '@iconify/react';
import Slide1 from "./slides/Slide1";
import SlideMap from "./slides/SlideMap";
import SlideAbout from "./slides/SlideAbout";
import SlideTech from "./slides/SlideTech";
import SlideCalculate from "./slides/SlideCalculate";
import SlideWhatToDo from "./slides/SlideWhatToDo";
import Slide2 from "./slides/Slide2";
import SlideSpheres from "./slides/SlideSpheres";
import SlideTestimotion from "./slides/SlideTestimotion";
import SlideStatusTestimotion from "./slides/SlideStatusTestimotion";
import SlideAboutDevice from "./slides/SlideAboutDevice";
import SlideHPV from "./slides/SlideHPV";
import SlideAdvantages from "./slides/SlideAdvantages";
import SlideFeedbacks from "./slides/SlideFeedbacks";
import SlideVideoFeedbacks from "./slides/SlideVideoFeedbacks";
import SlideAboutSupport from "./slides/SlideAboutSupport";
import SlideCases from "./slides/SlideCases";

const slideImages = [
  {
    url: "images/slide_2.jpg",
    caption: "Slide 1",
  },
  {
    url: "images/slide_3.jpg",
    caption: "Slide 2",
  },
  {
    url: "images/slide_4.jpg",
    caption: "Slide 3",
  },
];

function App() {
  const [data, setData] = useState({});
  const [error, setError] = useState({});
  const {email} = useParams();


  useEffect(() => {
    fetchData();
  }, []);


  const fetchData = async () => {
    
    try {
      let res = await fetch(process.env.REACT_APP_API_URL + "/view/" + email, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        },
      });
      let data = await res.json();
      setData(data);
      console.log(data);
    } catch (error) {
      setError(error);
      console.log(error);
    }
  };




  return (
    <YMaps>
      <div className="App">
        {error?.message ? (
          <div className="slide-container">
            <div className="slide">
              <h2> </h2>
              <span>{error?.message}</span>
            </div>
          </div>
        ) : (
          <div className="slide-container">
            <Swiper
              allowTouchMove={false}
              modules={[Navigation, Pagination, Scrollbar, A11y]}
              spaceBetween={50}
              slidesPerView={1}
              navigation
              pagination={{ clickable: true }}
              onSlideChange={() => console.log("slide change")}
              onSwiper={(swiper) => console.log(swiper)}
            >
            <SwiperSlide>
              <Slide1 user={data?.user}></Slide1>
            </SwiperSlide>
            <SwiperSlide>
              <Slide2 user={data?.user}></Slide2>
            </SwiperSlide>
            <SwiperSlide >
                  <SlideAbout user={data?.user}/>
            </SwiperSlide>
            <SwiperSlide >
                  <SlideTech user={data?.user}/>
            </SwiperSlide>
            <SwiperSlide >
                <SlideMap data={data}/>
            </SwiperSlide>
            <SwiperSlide >
                <SlideSpheres data={data}/>
            </SwiperSlide>
            <SwiperSlide >
                <SlideTestimotion data={data}/>
            </SwiperSlide>
            <SwiperSlide >
                <SlideWhatToDo user={data?.user}/>
            </SwiperSlide>
            <SwiperSlide >
                <SlideStatusTestimotion user={data?.user}/>
            </SwiperSlide>
            <SwiperSlide >
                <SlideAboutDevice user={data?.user}/>
            </SwiperSlide>
            <SwiperSlide >
                <SlideHPV user={data?.user}/>
            </SwiperSlide>
            <SwiperSlide >
                <SlideCalculate user={data?.user}/>
            </SwiperSlide>
            <SwiperSlide >
                <SlideAboutSupport user={data?.user}/>
            </SwiperSlide>
            <SwiperSlide >
                <SlideCases user={data?.user}/>
            </SwiperSlide>
            <SwiperSlide >
                <SlideVideoFeedbacks user={data?.user}/>
            </SwiperSlide>
            <SwiperSlide >
                <SlideFeedbacks user={data?.user}/>
            </SwiperSlide>
            <SwiperSlide >
                <SlideAdvantages user={data?.user}/>
            </SwiperSlide>
            </Swiper>
          </div>
        )}
      </div>
    </YMaps>
  );
}

export default App;
